
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: .3s linear;
}

body, #root {
  width: 100%;
  height: 100%;
  margin-top: 50px;
}
#root {
  display: flex;
  justify-content: center;
}
body {
  background: #150217;
}

a {
  text-decoration: none;
  border: none;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
}

nav, ul {
  width: 100%;
  height: 50px;
  justify-content: center;
  background: black;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style: none;
}
nav ul li {
  flex-grow: 1;
  max-width: 250px;
  text-align: center;
  height: 100%;
}
nav ul li a {
  float: left;
  width: 100%;
  height: 100%;
  text-align: center;
  background: black;
  line-height: 50px;
  font-size: 1.2em;
  letter-spacing: 2.5px;
  color: white;
}
nav ul li a:hover {
  background: #150217;
}
.active {
  background: #150217;
}


.convert-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  min-height: 400px;
  margin-top: 7px;
  background: rgba(42, 46, 42, .7);
  color: white;
}
.convert-section p {
  margin: 40px;
  font-size: 1.6rem;
}
.convert-section label {
  font-size: 12px;
  margin: 4px;
  color: rgb(29, 92, 29);
}

.convert-section form, .convert-section input {
  width: 80%;
  max-width: 400px;
  margin-bottom: 30px;
  height: 43px;
  background: rgba(123, 119, 119, .8);
  font-size: 1.2rem;
  color: antiquewhite;
  text-indent: 9px;
  outline: none;
  border: none;
}

.convert-section button {
  height: 64px;
  width: 50%;
  min-width: 140px;
  max-width: 310px;
  font-size: 1.3rem;
  margin: 12px;
  outline: none;
  border-radius: 60px;
  border: none;
  color: beige;
  letter-spacing: 2px;
  background: rgb(137, 10, 35);
}

.convert-section button:focus, .convert-section input:focus {
  outline: none;
  border: none;
}
.convert-section input:focus {
  background: rgba(91, 90, 90, 0.8);;
}
.convert-section button:hover {
  letter-spacing: 1.5px;
  background: rgb(159, 9, 39);
}

.convert-section h4 {
  margin: 12px;
  color: goldenrod;
  cursor: pointer;
}
.convert-section h4:hover {
  color: blue;
}
.convert-section span {
  color: tomato;
  font-size: 15px;
  margin-bottom: 12px;
}
.convert-section button.copy-btn {
  height: 20px;
  width: 50px;
  font-size: 12px;
  background: grey;
  border-radius: 2px;
}